@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');
@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css');


* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    overflow-x: hidden;
    font-family: 'Poppins', sans-serif;
}
 
main{
    display: flex;
    width: 100vw;

    aside {
        position: fixed;
        background-color: white;
        border: solid;
        border-width: 1px;
        border-radius: 0 20px 20px 0;
        border-color:rgba(71, 71, 71, 0.23);
        height: 100vh;
        width: 210px;
        padding: 2% 0;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;

        a {
           width: 100%;
           display: flex;
           justify-content: left;
           align-items:center;
           padding: 2% 20% 0%;
           
           span {
            margin-left: 4%;
           }
            
           &:hover {
            background-color: #be123d14;
            border-right: 2px solid #BE123C;
            color: #BE123C;
            font-weight: bold;
           }

           &:first-child {
                margin-bottom: 15%;
                padding: 2% 5% 0%;
                
                &:hover {
                    background-color: transparent;
                    border-right: none;
                }    
           }

           &:last-child {
            margin-top: auto;
           }
        }
    }

    section {
        margin-left: 210px;
        padding: 2%;

        .video-wrapper {
            height: 50vh;
            border-radius: 16px;

            &>* {
                height: 100%;
                overflow: hidden;
            }
        }

        .title-bar {
            display: flex;
            align-items: center;
            height: fit-content;
            padding: 1% 0;

            &>* {
               margin-right: 1%;
            }

            span:last-child {
                margin-left: auto;
                display: flex;
                align-items: center;
            }
        }

        article {
            display: flex;
            align-items: flex-start;
            margin-top: 1%;

            div:first-child {
                width: 75%;
                padding: 0 2% 0 0;

                .cast-and-crew {
                    display: flex;
                    flex-direction: column;
                    margin-top: 1%;

                    span {
                        margin: 1% 0;
                    }
                }
            }

            .unimportant {
                position: relative;
                // display: flex;
                width: 24%;
                height: 100%;
                flex-direction: column;
                justify-content: flex-start;

                button {
                    width: 100%;
                    padding: 1.5% 0;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    border-width: 1px;
                    border-radius: 8px;
                    border-color: #BE123C;
                    margin-bottom: 2.5%;

                    &:first-child {
                        background-color: #BE123C;
                        color: white;
                    }
                    &:last-child {
                        background-color: #be123db9;
                        color: white;
                    }
                }

                div {
                    position: relative;
                    margin-top: 3%;
                    border-radius: 10px;
                    overflow: hidden;
                    display: flex;
                    justify-content: left;
                    align-items: center;
                    height: 25vh;

                    img {
                        width: 100%;
                        height: 100%;
                    }

                    span {
                        position: absolute;
                        left: 0;
                        bottom: 0;
                        width: 100%;
                        height: 20%;
                        padding-left: 3px;
                        background-color: rgba(0, 0, 0, 0.60);
                        display: flex;
                        justify-content: left;
                        align-items: center;
                        overflow: hidden;
                        white-space: nowrap;
                        text-overflow: ellipsis;

                        p {
                            color: white;
                            font-size: 0.75rem;
                        }
                    }
                }
            }
        }

        .red-text {
            color: #BE123C
        }
    }
}


@media only screen and (max-width:1200px) {
    main {
        section {
            article {
                div:first-child {
                    width: 100%;
                    padding: 0 auto;
                }
            }
            .unimportant {
                display: none;
            }
        }
        
    }
    
}

@media only screen and (max-width:900px) {
    main {
        aside{
            width: 60px;
            a {
                padding: 20% 0;
                justify-content: center;

                span {
                    display: none;
                }

                &:first-child {
                    display: flex;
                    justify-content: center;
                    // transform: scale(2);
                    margin-bottom: 30%;

                }   
            }
        }
        section {
            margin-left: 60px;
            margin-top: 5%;
            padding: 0% 5%; 
        }
    }
}
@media only screen and (max-width:640px) {
    main {
        aside{
            width: 60px;
            a {
                padding: 20% 0;
                justify-content: center;

                span {
                    display: none;
                }

                &:first-child {
                    display: flex;
                    justify-content: center;
                    // transform: scale(2);
                    margin-bottom: 30%;

                }   
            }
        }
        section {
            margin-left: 60px;
            margin-top: 5%;
            padding: 0% 5%; 

            .title-bar {
                position: relative;
                flex-direction: column;
                align-items:flex-start;

                i {
                    display: none;
                    &:last-child {
                        display: inline-block;
                    }
                }

                span:last-child {
                    position: absolute;
                    top: 5%;
                    right: 0;
                    margin: 0;
                }
            }

            article {
                margin-top: 5%;
            }
        }
    }
}
